.aboutPage{
    width: 100vw;
    margin-top: 100vh;
    display: flex;
    align-items: center;
    .aboutUs{
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        align-items: center;
        background: #fff;
        box-shadow: 1px 1px 40px #00000010;
        width: 100vw;
        min-height: 500px;
        .left{
            background: thistle;
            width: 85%;
            height: 400px;
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        .right{
            width: 90%;
            text-align: left;
            padding-bottom: 50px;
            h1{
                font-size: 2.4rem;
            }
            p{
                font-size: 20px;
                line-height: 30px;
                color: #373737;
            }
            .second{
                padding-bottom: 40px;
            }
            a{
               text-decoration: none;
               color: #fff;
               background: $base-color;
               padding: 15px 20px; 
               
            }
        }
    }
}