.event{
    margin-top: 100vh;
    width: 100vw;
    .eventGrid{
        display: grid;
        grid-template-columns: repeat(auto-fit,80%);
        justify-content: center;
        gap: 20px;
        padding-bottom: 50px;
        .item{
            box-shadow: 2px 2px 10px  #02020211;
            display: flex;
            height: 370px;
            .left{
                height: 100%;
                width: 50%;
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            .right{
                width: 50%;
                padding-bottom: 20px;
                text-align: left;
                padding-left: 15px;
                h3{
                    width: 90%;
                    font-size: 22px;
                    color: gray;
                }
                p{
                    font-size: 18px;
                }
            }
        }
    }
}

@media screen and (max-width:600px) {
    .event{
        .eventGrid{
            grid-template-columns: repeat(auto-fit,95%);
            row-gap: 30px;
            .item{
                height: auto;
                flex-direction: column;
                .left{
                    width: 100%;
                }
                .right{
                    width: 100%;
                }
            }
        }
    }
}