.header{
    position: absolute;
    width: 100vw;
    min-height: 70vh;
    background: url(../../../public/imgs/bg1.png) no-repeat;
    background-size: cover;
    background-position: center;
    top: 179px;
    display: flex;
    justify-content: center;
    align-items: center;
    .layer{
        height: 100%;
        width: 100%;
        background: rgba(0, 0, 0, 0.375);
        position: absolute;
        backdrop-filter: blur(1px);
    }
    .hText{
        position: relative;
        z-index: 10;
        text-align: left;
        width: 550px;
        padding: 15px;
      
        p{
            font-size: 20px;
            color: #f2f2f2;
            line-height: 30px;
        }
        h1{
            font-size: 3.2em;
            color: #fff;
        }

    }
}

@media screen and (max-width:480px){
    .header{
        .hText{
            h1{
                font-size: 3em;
            }
        }
    }
}