.notFound{
    padding-bottom: 50px;
    width: 100vw;
    h1{
        font-size: 10em;
        background: $linear;
        background-clip: text;
        -webkit-background-clip: text;
        color: transparent;
        margin-top: -20px;
    }
    a{
        text-decoration: none;
        font-size: 30px;
        background: $base-color;
        color: #fff;
        padding: 10px 20px;
        border-radius: 50px;
        
    }
}
@media screen and (max-width:490px){
    .notFound{
        h1{
            font-size: 8em;
        }
    }
}