.footer{
    width: 100vw;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: #f1f1f1;
   .main{
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;

    .left{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        div{
            display: flex;
            align-items: center;
            margin-left: -20px;
            div{
                display: flex;
                flex-direction: column;
                
            }
        }
        p{
            margin-top: -10px;
            font-size: 20px;
            color: rgb(80, 80, 80);
        }
        p.desc{
            margin-top: 20px;
            width: 350px;
            font-size: 20px;
            line-height: 30px;
            text-align: left;
        }
    }
    .right{
        h1{
            font-size: 2.3rem;
            color: #424242;
        }
        .social{
            display: flex;
            justify-content: center;
            align-items: center;
           a{
            text-decoration: none;
            .bx{
                font-size: 2.5em;
                margin: 0 5px;
                transition: scale .3s;
                &:hover{
                    scale: 1.2;
                }
            }
            &:nth-child(1){color: rgb(4, 156, 1);}
            &:nth-child(2){color: #4e77cf;}
            &:nth-child(3){color: #55acee;}
            &:nth-child(4){color: #ff0084;}
            &:nth-child(5){color: rgb(65, 222, 41);}
            &:nth-child(6){color: #007ee5;}
           }
        }
    }
   }
   .foot{
    background: rgb(230, 230, 230);
    width: 100vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p{
        margin-left: 5%;
        margin-right: 5%;
        display: flex;
        align-items: center;
        .bx{
            color: red;
            font-size: 20px;
            padding: 0 5px;
        }
    }
   }
}

@media screen and (max-width:850px){
    .footer{
        .main{
            flex-direction: column;
        }
    }
}
@media screen and (max-width:500px){
    .footer{
       .foot{
        flex-direction: column;
        justify-content: center;
       }
    }
}