.galerie{
    width: 100vh;
    height: max-content;
    main{
        width: 100vw;
        margin-top: 180px;
        flex-direction: column;
        display: flex;
        justify-content: center;
        .top{
            width: 100vw;
            padding: 50px 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            h1{
                font-size: 3rem;
                position: relative;
                &:before{
                    content: '';
                    position: absolute;
                    top: 70px;
                    height: 3px;
                    width: 80%;
                    background: $base-color;
                    border-radius: 20px;
                }
            }
            p{
                width: 70%;
                text-align: center;
                font-size: 20px;
                color: rgb(83, 83, 83);
            }
        }
        .gridGallery{
            width: 100vw;
            display: grid;
            grid-template-columns: repeat(auto-fit,250px);
            justify-content: center;
            gap: 20px;
            padding-bottom: 50px;
            .item{
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
    }
}

@media screen and (max-width:500px){
    .galerie{
        main{
            .gridGallery{
                grid-template-columns: repeat(auto-fit,90%);
            }
        }
    }
}