.navigation{
    position: fixed;
    width: 100vw;
    min-height: 150px;
    z-index: 1000;
    nav{
        .miniNav{
            height: 50px;
            background: #f3f3f3;
            position: fixed;
            top: 0;
            width: 100vw;
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            .social{
                
                a{
                    color: #212121;
                    text-decoration: none;
                    .bx{
                        font-size: 1.5em;
                        margin: 0 5px;
                        transition: scale .3s;
                        &:hover{
                            scale: 1.2;
                        }
                    }
                    &:nth-child(1){color: #4e77cf;}
                    &:nth-child(2){color: #55acee;}
                    &:nth-child(3){color: #ff0084;}
                    &:nth-child(4){color: red;}
                    &:nth-child(5){color: #007ee5;}
                }
            }
        }
        .navBar{
            position: fixed;
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: space-between;
            background: $base-color;
            top: 50px;
            .logo{
                display: flex;
                align-items: center;
                img{
                    width: 150px;
                }
                div{
                    text-align: left;
                    color: #fff;
                    user-select: none;
                    h1{
                        margin-left: -10px;
                    }
                    p{
                        margin-top: -15px;
                    }
                }
            }
            .bx-menu{
                color: #fff;
                font-size: 2.5em;
                cursor: pointer;
                display: none;
            }
            .menu{
                margin-right: 5%;
                display: flex;
                align-items: center;
                .bx-x{
                    font-size: 2.5em;
                    color: #fff;
                    position: absolute;
                    top: 20px;
                    right: 5%;
                    cursor: pointer;
                    display: none;
                }
                a{
                    position: relative;
                    margin: 0 15px;
                    text-transform: uppercase;
                    text-decoration: none;
                    color: #f1f1f1;
                    &::before{
                        content: '';
                        height: 2px;
                        width: 0%;
                        position: absolute;
                        top: 20px;
                        background: #f1f1f1;
                        transition: width .3s;
                    }
                    &:hover::before{
                        width: 85%;
                    }
                }
            }
        }
    }
}


@media screen and (max-width:800px) {
    .navigation{
       nav{
        .navBar{
            .bx-menu{
                display: block;
                margin-right: 15px;
            }
            .menu{
                .bx-x{
                    display: block;
                }
                flex-direction: column;
                justify-content: center;
                position: fixed;
                right: 0;
                top: 0;
                height: 100vh;
                width: 0;
                margin-right: 0;
                background: #000000a6;
                -webkit-backdrop-filter: blur(7px);
                backdrop-filter: blur(7px);
                z-index: 200;
                overflow-x: hidden;
                transition: width .2s;
                a{
                    margin-top: 10px;
                    font-size: 1.5rem;
                    &::before{
                        top: 30px;
                    }
                }
                &.active{
                    width: 100vw;
                }
            }
        }
       }
    }
}

@media  screen and (max-width:490px) {
    .navigation{
        nav{
            .miniNav{
                flex-direction: column;
                height: auto;
            }
            .navBar{
                top:82px;
            }
        }
    }
}