.home{
    min-height: 100vh;
    margin-top: 100vh;
    main{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        // about script 
        .aboutUs{
            display: flex;
            justify-content: space-between;
            flex-direction: row-reverse;
            align-items: center;
            background: #fff;
            box-shadow: 1px 1px 40px #00000010;
            width: 80%;
            min-height: 500px;
            margin-top: 50px;
            .left{
                background: $linear;
                width: 45%;
                height: 100%;
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            .right{
                width: 40%;
                text-align: left;
                padding-bottom: 20px;
                h1{
                    font-size: 2.4rem;
                }
                p{
                    font-size: 20px;
                    line-height: 30px;
                    color: #373737;
                    padding-bottom: 20px;
                }
                a{
                    text-decoration: none;
                    color: #fff;
                    background: $base-color;
                    padding: 15px 30px;
                }
            }
        }
        // about script 
        .firstDiv{
            display: flex;
            justify-content: space-between;
            align-items: center;
            background: #fff;
            box-shadow: 1px 1px 40px #00000010;
            width: 80%;
            min-height: 500px;
            margin-top: 50px;
            .left{
                background: $linear;
                width: 45%;
                height: 100%;
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }
            .right{
                width: 40%;
                text-align: left;
                padding-bottom: 20px;
                h1{
                    font-size: 2.4rem;
                }
                p{
                    font-size: 20px;
                    line-height: 30px;
                    color: #373737;
                }
            }
        }
        .ourMission{
            width: 100vw;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            margin-top: 50px;
            padding-bottom: 40px;
            .top{
                h1{
                    position: relative;
                    font-size: 2.5rem;
                    &::before{
                        content: '';
                        position: absolute;
                        top: 50px;
                        width: 50%;
                        height: 3px;
                        background-color: $base-color;
                    }
                }
                p{
                    font-size: 25px;
                    padding-bottom: 20px;
                }
            }
            .elements{
                width: 100vw;
                display: grid;
                grid-template-columns: repeat(auto-fit,300px);
                justify-content: center;
                gap: 20px;
                .mission{
                    display: flex;
                    flex-direction: column;
                    box-shadow: 1px 1px 40px #00000013;
                    .img{
                        width: 100%;
                        height: 200px;
                        background: $base-color;
                        img{
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }
                    }
                    .desc{
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        flex-direction: column;
                        padding-bottom: 20px;
                        h2{
                            text-align: left;
                            font-size: 20px;
                            width: 80%;
                        }
                        .descText{
                            text-align: left;
                            width: 80%;
                            margin-top: -5px;
                            color: #373737;
                        }
                        button{
                            padding: 10px 20px
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width:1000px){
    .home{
        main{
            .aboutUs{
                flex-direction: column;
                width: 95%;
                margin-top: 0;
                .left{
                    width: 100%;
                    height: 350px;
                }
                .right{
                    width: 95%;                   
                    padding-bottom: 30px;
                    h1{
                        width: 90%;
                    }
                    p{
                        width: 95%;
                    }
                }
            }
            .firstDiv{
                flex-direction: column;
                width: 95%;
                margin-top: 0;
                .left{
                    width: 100%;
                    height: 350px;
                }
                .right{
                    width: 95%;                   
                    padding-bottom: 30px;
                    h1{
                        width: 90%;
                    }
                    p{
                        width: 95%;
                    }
                }
            }
        }
    }
}