$base-color: #1429a6;
$linear: linear-gradient(75deg,#00c6ff,$base-color);
html,body{
    overflow-x: hidden;
    scroll-behavior: smooth;
}
body{
    margin: 0;
    padding: 0;
    text-align: center;
    font-family: sans-serif;
    overflow-x: hidden;
}
::-webkit-scrollbar{
    width: 10px;
    background: #fff;
}
::-webkit-scrollbar-thumb{
    background: $base-color;
    border-radius: 20px;
}
button{
    padding: 15px 25px;
    border: none;
    outline: none;
    background: $base-color;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
    margin-top: 10px;
    // border-radius: 50px;
    &:hover{
    background: #0f1e77;
    }
}
span{
    color: $base-color;
    font-weight: 600;
}
